
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    modelValue: Boolean,
    icon: { type: String, default: "" },
    text: { type: String, default: "" },
  },
  emits: ["update:modelValue"],
  setup(props, { emit, attrs }) {
    function toggle() {
      emit("update:modelValue", !props.modelValue);
    }
    return { toggle, attrs };
  },
});
